export default {
	

	data: function() {
		return {
			mv: [],
			srcc: '',
			aud: {},
			index: 0,
			clip: 0,
			clips: 0,
			author: '',
			music_pic: '',
			title: '',
			durations:'',
			currentTimes:'',				
			musics:[],
			serach:"success",
			ro:false,
			tf:false,
			stopImage:"../../static/music/next.png",
			xhImage:"../../static/music/xh.png",
			hq:"",
			currentindex:null
			
				
			
				
			



		}
	},
	onShow:function(){
		this.aud = uni.createInnerAudioContext()	
	},
	onReady: function() {			
		uni.request({
			url: 'https://api.apiopen.top/searchMusic?name=口是心非',
			success: (res) => {
				this.mv = res.data.result
				this.music_pic=this.mv[0].pic
				this.title=this.mv[0].title
				this.author=this.mv[0].author
				this.srcc=this.mv[0].url
				
				
			}
		})
		
	},
	onBackPress() {
		uni.switchTab({
			url:"wx"
		})
	},
	
	methods: {
		huoqu:function(event){
			this.hq=event.target.value
			
			
		},
		ss:function(){
			uni.request({
				url: 'https://api.apiopen.top/searchMusic?name='+this.hq,
				success: (res) => {
					this.mv = res.data.result
					this.music_pic=this.mv[0].pic
					this.title=this.mv[0].title
					this.author=this.mv[0].author
					this.srcc=this.mv[0].url						
					
				}
			});
		},
		zz: function(i, $event) {				
					this.aud.stop()											
					this.aud.src = this.mv[i].url
					this.author = this.mv[i].author
					this.title = this.mv[i].title
					this.music_pic = this.mv[i].pic
					this.index = i	
					this.stopImage="../../static/music/bf.png"
					this.aud.play()						
					this.ro=true
					this.tf=true
					this.aud.onEnded(()=>{
						this.ro=false
						this.tf=false
						this.stopImage="../../static/music/next.png"
					})
					this.aud.onPlay(()=>{
						this.currentindex=i
					})
																																																							
		},
		
		
		stop: function(e) {
			var m = ++this.clips
			if (m % 2 != 0) {
				this.aud.play()
				this.ro=true
				this.tf=true
				this.stopImage="../../static/music/bf.png"
				
			} else {					
				this.aud.pause()
				this.ro=false
				this.tf=false
				this.stopImage="../../static/music/next.png"
				
			}


		},
		next: function(e) {
			this.aud.stop() //关键，必须等当前音乐停止了才可以继续下一首
			var a = ++this.index
			console.log(a)
			if(a==this.mv.length){
				uni.showToast({
					title:"最后一首了",
					icon:"none"
				})
			}
			this.aud.src = this.mv[a].url
			this.author = this.mv[a].author
			this.title = this.mv[a].title
			this.music_pic = this.mv[a].pic
			this.aud.play()
			this.ro=true
			this.tf=true
			this.stopImage="../../static/music/bf.png"
			this.aud.onPlay(()=>{
				this.currentindex=a
			})
		},
		back: function() {
			this.aud.stop()
			var a = --this.index
			console.log(a)
			if(a==-1){
				uni.showToast({
					title:"这是第一首了",
					icon:"none",						
				})
			}
			this.aud.src = this.mv[a].url
			this.author = this.mv[a].author
			this.title = this.mv[a].title
			this.music_pic = this.mv[a].pic
			this.aud.play()
			this.ro=true
			this.tf=true
			this.stopImage="../../static/music/bf.png"
			this.aud.onPlay(()=>{
				this.currentindex=a
			})
		},
		loops: function() {
			var mm = ++this.clip								
			if(mm%2!=0){					
				
			this.aud.loop=false	
			this.xhImage="../../static/music/xh1.png"
			}else{				
			
			this.aud.loop=true
			this.xhImage="../../static/music/xh.png"
			
			}
		}
	}

}
